import React, { Component } from 'react';
import { Drawer,Carousel, Button} from 'antd';
class Detail extends Component {
    constructor(props) {
        super(props);
        this.state = {  }
    }
    componentDidUpdate(prevProps, prevState) {
        if(this.refs.carousel){
            this.refs.carousel.goTo(0)
        }
    }
    render() { 
        return ( 
            <Drawer
                placement="right"
                onClose={this.props.onDetailClose}
                visible={this.props.detailVisible}
            >
                <Carousel autoplay ref='carousel'>
                    {this.props.item.img.map((img,index)=>(
                        <div key={index}>
                            <div className='cover' style={{backgroundImage:'url('+img+')'}}></div>
                        </div>
                    ))}
                </Carousel>
                <div className='drawer-content'>
                    <h2>{this.props.item.title}</h2>
                    <p>{this.props.item.description}</p>
                    {this.props.item.link?
                    (<p><Button type="primary" ghost><a target='_blank' rel="noopener noreferrer" href={this.props.item.link}>Visit Website</a></Button></p>):''}
                    <h4>What I did:</h4>
                    <p>{this.props.item.contribution}</p>
                </div>
            </Drawer>
         );
    }
}
 
export default Detail;